import "../../../src/App.scss";
import { useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/effect-fade";
function MainProducts() {
  let state = useSelector((state) => {
    return state;
  });

  return (
    <section className="mainproducts">
      <p>Best Notes</p>
      <Swiper
        grabCursor={true}
        loop={false}
        speed={800}
        slidesPerView={3}
        className="swiper mainProductsSwiper"
        breakpoints={{
          1024: {
            slidesPerView: 3,
          },
          768: {
            slidesPerView: 2,
          },
          0: {
            slidesPerView: 1,
          },
        }}
      >
        {state.best.map((a, i) => {
          return (
            <SwiperSlide className="swiper-slide" key={i}>
              <div className="mainproducts-div">
                <Link to={`/perfume/${a.id}`}>
                  <img src={`${a.img}`} />
                  <p className="mainproducts-name">{a.name}</p>
                  <p className="mainproducts-price">
                    {Number(a.price).toLocaleString()}원
                  </p>
                </Link>
              </div>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </section>
  );
}

export default MainProducts;
