import React from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Autoplay, Pagination, EffectFade } from "swiper";

import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/effect-fade";

export default function MainBg() {
  let bg = [
    {
      img: "https://kinfolk.kr/wp-content/uploads/2022/05/kinfolk-notes-main_magazine.png",
    },
    {
      img: "https://kinfolknotes.com/wp-content/uploads/2024/02/KakaoTalk_Photo_2024-02-02-18-22-45-scaled.jpeg",
    },
    {
      img: "https://www.kinfolk.com/wp-content/uploads/2023/12/K50_Cover.jpg",
    },
  ];

  return (
    <Swiper
      effect="fade"
      autoplay={{
        delay: 3000,
      }}
      speed={1500}
      grabCursor={true}
      centeredSlides={true}
      loop={true}
      fadeEffect={{ crossFade: true }}
      modules={[Autoplay, EffectFade, Pagination]}
      className="MainBgSwiper"
    >
      {bg.map((a, i) => {
        return (
          <SwiperSlide className="swiper-slide" key={i}>
            {/* <Link to={a.type}> */}
            <div style={{ width: "100%", height: "100%" }}>
              <img src={`${a.img}`} />
            </div>
            {/* </Link> */}
          </SwiperSlide>
        );
      })}
    </Swiper>
  );
}
