////////////////////////////////////////////// 상품 데이터//////////////////////////////////////////////////////

let cart = createSlice({
  name: "cart",
  initialState: [],
  reducers: {
    count(cart, actions) {
      let { id, num } = actions.payload;
      let 번호 = cart.findIndex((a) => a.id === id);
      if (번호 !== -1) {
        cart[번호].count += num;
      }
    },
    countAdd(cart, actions) {
      let 번호 = cart.findIndex((a) => a.id === actions.payload);
      if (번호 !== -1) {
        cart[번호].count++;
      }
    },

    countMinus(cart, actions) {
      let 번호 = cart.findIndex((a) => a.id === actions.payload);
      if (번호 !== -1) {
        cart[번호].count--;
      }
    },
    del(cart, actions) {
      cart.splice(actions.payload, 1);
    },
    add(cart, actions) {
      cart.push(actions.payload);
    },
  },
});

let result = createSlice({
  name: "result",
  initialState: [],
  reducers: {
    resultadd(result, actions) {
      result.splice(0);
      result.push(actions.payload);
    },
  },
});

export let { count, add, del, countAdd, countMinus } = cart.actions;

export let { resultadd } = result.actions;

export default configureStore({
  reducer: {
    cart: cart.reducer,
    best: best.reducer,
    mist: mist.reducer,
    perfume: perfume.reducer,
    result: result.reducer,
  },
});
