import { Link } from "react-router-dom";

function MainStory() {
  return (
    <section className="mainStory">
      <img
        src="https://kinfolknotes.com/wp-content/uploads/2023/09/KakaoTalk_Photo_2023-09-21-11-05-01-001.jpeg"
        width="100%"
      />
      <div className="mainstory-div">
        <p>"My own story with scent"</p>
        <Link to={"/story"}>More</Link>
      </div>
    </section>
  );
}

export default MainStory;
